<template>
  <div class="tools-container">
    <template v-if="!currentTool">
      <h1>Â∑•ÂÖ∑ÁÆ±</h1>
      <div class="tools-grid">
        <div 
          v-for="tool in tools" 
          :key="tool.id" 
          class="tool-card"
          @click="selectTool(tool)"
        >
          <div class="tool-icon">{{ tool.icon }}</div>
          <div class="tool-title">{{ tool.name }}</div>
          <div class="tool-description">{{ tool.description }}</div>
        </div>
      </div>
    </template>

    <template v-else>
      <div class="tool-container-with-back">
        <button @click="backToToolList" class="back-button">‚Üê ËøîÂõû</button>
        <div class="tool-content">
          <h2>{{ currentTool.name }}</h2>
          
          <component :is="currentTool.component"></component>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { ref, markRaw } from 'vue'

export default {
  name: 'ToolView',
  setup() {
    const tools = [
      {
        id: 'ip-lookup',
        name: 'IPÂú∞ÂùÄÊü•ËØ¢',
        description: 'Ëé∑ÂèñÊÇ®ÁöÑÂÖ¨ÁΩëIPÂú∞ÂùÄ',
        icon: 'üåê',
        component: () => import('@/components/tools/IPLookupTool.vue')
      },
      {
        id: "anime-image-tool",
        name: "Âä®Êº´ÂõæÁâá",
        description: "Ëé∑ÂèñÂä®Êº´ÂõæÁâá",
        icon: "üç±",
        component: () => import('@/components/tools/AnimeImageTool.vue')
      }
      // ÂèØ‰ª•ÁªßÁª≠Ê∑ªÂä†ÂÖ∂‰ªñÂ∑•ÂÖ∑
    ]

    const currentTool = ref(null)

    const selectTool = async (tool) => {
      const module = await tool.component()
      currentTool.value = {
        ...tool,
        component: markRaw(module.default)
      }
    }

    const backToToolList = () => {
      currentTool.value = null
    }

    return {
      tools,
      currentTool,
      selectTool,
      backToToolList
    }
  }
}
</script>

<style scoped>

.tools-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  min-height: 100vh; /* Á°Æ‰øùÂÆπÂô®Ëá≥Â∞ëÊúâËßÜÂè£È´òÂ∫¶ */
}

h1, h2 {
  color: #333;
  margin-bottom: 30px;
}

.tools-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 20px;
  justify-content: center;
}

.tool-card {
  background-color: #f9f9f9;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.tool-card:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
}

.tool-icon {
  font-size: 3rem;
  margin-bottom: 10px;
}

.tool-title {
  font-weight: bold;
  margin-bottom: 5px;
  color: #2c3e50;
}

.tool-description {
  color: #7f8c8d;
  font-size: 0.9rem;
}

.tool-container-with-back {
  max-width: 100%; /* ‰øÆÊîπ‰∏∫100%‰ª•ÈÄÇÂ∫îÂõæÁâágrid */
  margin: 0 auto;
}

.back-button {
  background-color: #f1f1f1;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 20px;
  transition: background-color 0.3s ease;
}

.back-button:hover {
  background-color: #e1e1e1;
}

.tool-content {
  text-align: center;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .tools-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (max-width: 992px) {
  .tools-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 768px) {
  .tools-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .tools-grid {
    grid-template-columns: 1fr;
  }
}

</style>